import React from 'react';
import {Panel, Div, Button, Cell, Avatar} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
 

const fields = [
    {
        x: 540,
        y: 1133,
        value: "Hello World",
        font: "96px Arial",
        align: "center",
        color: "#FFFFFF"
    }
];

VKStories.init(connect);

//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }
  GenerateStory = () =>{
  VKStories.generateStoryFromTemplate(require("../img/forest.png"), fields)
    .then((story) => {
        console.log("работает")
})
    .catch(console.error);
}

  render() {
    return(
      <Panel id="modal">
        <Div id="avatar-container">
          <Avatar src={this.state.fetchedUser.photo_200} size={150}/>
        </Div>

         <Div id="data-container">
           <Cell onClick={this.GenerateStory} class="text" id="name">{this.state.fetchedUser.first_name+" "+this.state.fetchedUser.last_name}</Cell>
         </Div>

        

      
            <Button size="xl"  style={{"background":"linear-gradient(to right #4d6cb7 #182848)", "color": "#fff"}}>Поделиться</Button>
        </Panel>
    );
  }

}

export default Modal;